import React, {Component} from "react";
import OnlineUtils from "eshop/utils/OnlineUtils";
import OOVMonthlyPriceTag from "eshop/modules/simfree/components/OOVMonthlyPriceTag"

var ProductListOfferPriceTagCount = 0;

class ProductListOfferPriceTag extends Component {
    constructor(props){
        super(props);
        this.state = {countId: ProductListOfferPriceTagCount++};
    }

    getPaymentsFromProposition(){
        let plainPriceObj = OnlineUtils.getPaymentsForRole(this.props.proposition.payments, "")
        const bonusPriceObj = OnlineUtils.getPaymentsForRole(this.props.proposition.payments, this.props.contractRole);

        let basePrice = plainPriceObj.basePrice.originalGrossPrice;
        let convPrice = bonusPriceObj.basePrice.originalGrossPrice;

        let monthlyPayments = plainPriceObj.totalPayments.monthlyPayments;
        let monthlyPaymentsWithBonuses = bonusPriceObj.totalPayments.monthlyPayments;

        return {
                baseSummaryPrice: basePrice,
                convSummaryPrice: convPrice,
                monthlyPayments: monthlyPayments,
                monthlyPaymentsWithBonuses: monthlyPaymentsWithBonuses
            }
    }

    getPaymentsFromEntry(){
        let size = this.props.entry.monthlyPrices && this.props.entry.monthlyPrices.size
        return {
             baseSummaryPrice: this.props.entry.monthlyOldPrice,
             convSummaryPrice: this.props.entry.monthlyPrices[size-1],
             monthlyPayments: this.props.entry.monthlyOldPrices,
             monthlyPaymentsWithBonuses: this.props.entry.monthlyPrices,
             clientContext : true
        }
    }

    render() {
        let payments = null
        if (this.props.entry) {
            payments = this.getPaymentsFromEntry()
        } else if (this.props.proposition){
            payments = this.getPaymentsFromProposition()
        } else {
            return null
        }

        /*
           :(
        */
        let clientContext = this.props.clientContext || (this.props.contractRole ? true : false)
        return (
                <OOVMonthlyPriceTag
                    clientContext={clientContext}
                    loyaltyLength={this.props.loyaltyLength}
                    monthString={this.props.monthlyString || "mies."}
                    mainPriceClass={this.props.mainPriceClass}
                    alignClass={this.props.alignClass}
                    offerDiscountInfoDescription={this.props.offerDiscountInfoDescription}
                    offerDiscountInfoConvDescription={this.props.offerDiscountInfoConvDescription}
                    baseId={this.props.baseId}
                    isMagnum={false}
                    {...payments}
                />


        )
    }
}

export default ProductListOfferPriceTag;